import React, { Component } from 'react';
import StartPage from './startPage/StartPage';
import './Quiz.css'
import service from '../../../_services/user-question.service';

class Quiz extends Component {
  constructor(props){
    super(props);
    this.state = {
      categoryId: 0,
      category: {},
      isLoading: true,

    }
  };

 async componentDidMount(){
    let quizId = this.props.match.params.id;
    await service.getCategoryById(quizId).then(res=>{
      this.setState({
        categoryId:  quizId,
        category: res.data,
        isLoading: false,
      });
    })



  };

  render() {
    const { category, isLoading  } = this.state;

   return (
      <div className='quiz'>
      {!isLoading ? (
        <StartPage

          category= {category}

          properties = {this.props}

        />
          ) : (
            <p>Loading...</p>

          )
      }
       </div>
    );
  };
};

export default Quiz;
